import { Animation } from 'cc';
import { macro } from 'cc';
import { Sprite } from 'cc';
import { _decorator, Component, Node } from 'cc';
import { mjways } from './cMjways';

const { ccclass, property } = _decorator;

@ccclass('mjwaysMul')
export class mjwaysMul extends Component {
    @property({ type: [Node], tooltip: "" })
    public muls: Node[] = [];

    onLoad() {

    }

    protected start(): void {
    }

    init(){
        if(this.fortuneBn){
            this.playFortuneBn = true;
        }
        this.curMul = this.mulNums[0];
    }

    /** */
    private _fortuneBn: boolean = false;
    public get fortuneBn(): boolean {
        return this._fortuneBn;
    }
    public set fortuneBn(v: boolean) {
        this._fortuneBn = v;
        this.node.children[0].active = v;
        this.doMuls((node:Node)=>{
            node.children[0].active = v;
            let on:Sprite = node.getChildByName("on").getComponent(Sprite);
            on.enabled = !v;
            on.node.children[0].active = v;
            let num:Sprite = node.getComponent(Sprite);
            num.enabled = !v;
            num.node.children[0].active = v;
        })
        this.init();
    }

    private playFortuneBn:boolean = false;
    playFortune(){
        this.schedule(this.doFortune,0.2,macro.REPEAT_FOREVER)
    }

    private doFortune(){
        let index:number = this.mulNums.indexOf(this.curMul);
        index++;
        this.curMul = this.mulNums[index];
        if(this.curMul == this.mulNums[this.mulNums.length-1]){
            this.unschedule(this.doFortune);
            this.scheduleOnce(()=>{
                this.playFortuneBn = false;
                this.curMul = this.mulNums[0];
                mjways.main.playFortuneBack();
            },0.2)
        }
    }

    private doMuls(func:Function){
        for(let i=0;i<this.muls.length;i++){
            this.muls[i].active = true;
            func(this.muls[i],i);
        }
    }

    private get mulNums():number[]{
        if(this.fortuneBn){
            return [2,4,6,10];
        }else{
            return [1,2,3,5];
        }
    }

    /** */
    private _curMul:number;
    public get curMul() : number {
        return this._curMul
    }
    public set curMul(v : number) {
        if(this._curMul != v){
            this.playMulAudio(v);
        }
        this._curMul = v;
        let mulNums:number[] = this.mulNums;
        this.doMuls((node:Node,i:number)=>{
            let selectBn:boolean = mulNums[i]==v;
            node.getChildByName("select").active = selectBn;
            node.getChildByName("on").active = selectBn;
            let light:Animation =  node.getChildByName("light").getComponent(Animation);
            if(this.fortuneBn){
                light.node.active = selectBn;
                if(selectBn){
                    light.play();
                }
            }else{
                light.node.active = false;
            }
        })
    } 

    private playMulAudio(num:number){
        if(this.playFortuneBn == false){
            mjways.audio.playVoxMultiply(num)
        }
    }
}