import { _decorator, Component, AudioClip } from 'cc';
import { ModuleDef } from '../../scripts/ModuleDef';
import { mjways } from './cMjways';
const { ccclass, property } = _decorator;

@ccclass('mjwaysAudio')
export class mjwaysAudio extends Component {
    onLoad() {
    }

    start() {
        mjways.audio = this;
    }

    private urlArr: string[] = ["sound/bgm_mg", "sound/bgm_bonus_loop"];
    private bgmIndex: number;
    playBgm(index: number) {
        if (this.bgmIndex == index) return;
        this.bgmIndex = index;
        tgx.AudioMgr.inst.audio.stopMusic();
        tgx.AudioMgr.inst.audio.playMusicLoop(this.urlArr[index], ModuleDef.SLOTS_MJWAYS)
    }

    /***/
    playVoxMultiply(num: number) {
        let numArr: number[] = [2, 3, 4, 5, 6, 10];
        let index: number = numArr.indexOf(num);
        if (index == -1) return;
        tgx.AudioMgr.inst.audio.playEffect(EEFFECT.voxMultiply + num + "x", ModuleDef.SLOTS_MJWAYS);
        this.scheduleOnce(() => {
            tgx.AudioMgr.inst.audio.playEffect(EEFFECT.fsMultiplyRefresh, ModuleDef.SLOTS_MJWAYS);
        }, 0.5)
    }

    /***/
    playVoxPayout(index: number) {
        let arr: string[] = ["2tiao", "2tong", "3tong", "5tiao", "5tong", "8wan", "Baiban", "Hongzhong", "Facai"];
        tgx.AudioMgr.inst.audio.playEffect(EEFFECT.voxPayout + arr[index], ModuleDef.SLOTS_MJWAYS);
    }

    /** */
    playVoxPayoutMultiSym() {
        let arr: string[] = ["01", "02"];
        let index: number = Math.floor(Math.random() * arr.length);
        index = index == arr.length ? arr.length - 1 : index;
        tgx.AudioMgr.inst.audio.playEffect(EEFFECT.voxPayoutMultiSym + arr[index], ModuleDef.SLOTS_MJWAYS);
    }

    /** */
    playVoxRandom() {
        let arr: string[] = ["01", "02", "03", "04", "05", "06", "07", "08", "09", "10", "11", "12"];
        let index: number = Math.floor(Math.random() * arr.length);
        index = index == arr.length ? arr.length - 1 : index;
        tgx.AudioMgr.inst.audio.playEffect(EEFFECT.voxRandom + arr[index], ModuleDef.SLOTS_MJWAYS);
    }

    /** */
    playReelLoop(speedBn: boolean) {
        if (speedBn) {
            tgx.AudioMgr.inst.audio.playEffect(EEFFECT.reelQuickstop, ModuleDef.SLOTS_MJWAYS);
        } else {
            tgx.AudioMgr.inst.audio.playEffect(EEFFECT.reelLoop, ModuleDef.SLOTS_MJWAYS);
        }
    }
    /** */
    private playReelEndBn: boolean = false;
    playReelEnd(isSpeed: boolean = false) {
        if (this.playReelEndBn) return;
        this.playReelEndBn = true
        this.scheduleOnce(() => {
            this.playReelEndBn = false;
        }, 0.01)
        if (isSpeed) {
            tgx.AudioMgr.inst.audio.playEffect(EEFFECT.reelQuickstop, ModuleDef.SLOTS_MJWAYS);
        } else {
            tgx.AudioMgr.inst.audio.playEffect(EEFFECT.reelStopSingle, ModuleDef.SLOTS_MJWAYS);
        }
    }

    playFsDragonFly() {
        tgx.AudioMgr.inst.audio.playEffect(EEFFECT.fsDragonFly, ModuleDef.SLOTS_MJWAYS);
    }

    /**spawn wild */
    playSpawnWild() {
        tgx.AudioMgr.inst.audio.playEffect(EEFFECT.symbolWild, ModuleDef.SLOTS_MJWAYS);
    }
    /**spawn Scatter */
    playSpawnScatter() {
        tgx.AudioMgr.inst.audio.playEffect(EEFFECT.symbolScatter, ModuleDef.SLOTS_MJWAYS);
    }

    /** */
    playSymbolShrink() {
        tgx.AudioMgr.inst.audio.playEffect(EEFFECT.symbolShrink, ModuleDef.SLOTS_MJWAYS);
    }
    /**bigwin */
    private bigwinId: number;
    playBigwin() {
        tgx.AudioMgr.inst.audio.playEffect(EEFFECT.bgmBigwinMain, ModuleDef.SLOTS_MJWAYS).then((value) => {
            this.bigwinId = value;
        });
    }
    /**bigwin end */
    private bigwinEndId: number;
    playBigwinEnd() {
        tgx.AudioMgr.inst.audio.stopEffect(this.bigwinId, EEFFECT.bgmBigwinMain, ModuleDef.SLOTS_MJWAYS);
        tgx.AudioMgr.inst.audio.playEffect(EEFFECT.bgmBigwinEnd, ModuleDef.SLOTS_MJWAYS).then((value) => {
            this.bigwinEndId = value;
        });
    }
    stopBigwinEnd() {
        tgx.AudioMgr.inst.audio.stopEffect(this.bigwinEndId, EEFFECT.bgmBigwinEnd, ModuleDef.SLOTS_MJWAYS);
    }
}

export enum EEFFECT {
    voxMultiply = "sound/vox_extract/voxMultiply",//
    fsMultiplyRefresh = "sound/general_audio_extract/fsMultiplyRefresh",//
    voxPayout = "sound/vox_extract/voxPayout",//
    voxPayoutMultiSym = "sound/vox_extract/voxPayoutMultiSym",//
    voxRandom = "sound/vox_extract/voxRandom",//
    reelLoop = "sound/general_audio_extract/reelLoop",//
    reelQuickstop = "sound/general_audio_extract/reelQuickstop",//
    reelStopSingle = "sound/general_audio_extract/reelStopSingle",//
    bgmBigwinMain = "sound/general_audio_extract/bgmBigwinMain",//bigwin
    bgmBigwinEnd = "sound/general_audio_extract/bgmBigwinEnd",//bigwin end
    fsDragonFly = "sound/general_audio_extract/fsDragonFly",//fortuneAni
    symbolScatter = "sound/general_audio_extract/symbolScatter",
    symbolShrink = "sound/general_audio_extract/symbolShrink",
    symbolWild = "sound/general_audio_extract/symbolWild",
}
