import { SpriteFrame } from 'cc';
import { Sprite } from 'cc';
import { _decorator, Component, Node } from 'cc';
import { Label } from 'cc';
import { Color } from 'cc';
import { Vec3 } from 'cc';
import { tween } from 'cc';
import { macro } from 'cc';
import { Prefab } from 'cc';
import { instantiate } from 'cc';
import { Num } from '../../core_tgx/framework/util/Num';
import { Animation } from 'cc';
import { mjways } from './cMjways';
const { ccclass, property } = _decorator;

@ccclass('mjwaysFreeStart')
export class mjwaysFreeStart extends Component {

    @property({ type: Label, tooltip: "" })
    public count: Label = null!;
    @property({ type: Label, tooltip: "" })
    public progress: Label = null!;
    @property({ type: Node, tooltip: "" })
    public btn: Node = null!;
    onLoad() {
        this.showBtnBn = false;
        this.progress.string = "0";
        this.clickBn = false;
    }

    protected start(): void {

    }

    doProgress() {
        tween(this.progress)
            .to(1, { string: "100" })
            .call(() => {
                this.showBtnBn = true;
            })
            .start();
    }

    setParent() {
        this.node.parent = mjways.slotCtrl.node;
        this.doProgress();
        this.scheduleOnce(this.close, 5);
        this.count.string = mjways.lotteryRes.results[1].leftFreeCount + "";
        mjways.slotCtrl.lastNum = mjways.lotteryRes.results[1].leftFreeCount;
        mjways.bg.fortuneBn = true;
    }

    /** */
    private _showBtnBn: boolean = false;
    public get showBtnBn(): boolean {
        return this._showBtnBn;
    }
    public set showBtnBn(v: boolean) {
        if (v && v != this._showBtnBn) {
            this.btn.active = true;
            this.btn.scale = new Vec3(0, 0);
            tween(this.btn)
                .to(0.5, { scale: new Vec3(0.8, .8) })
                .start();
        }
        this._showBtnBn = v;
        this.progress.node.parent.active = !v;
        this.btn.active = v;
        if (v && this.node.parent) {
            this.node.setSiblingIndex(this.node.parent.children.length - 1);
        }
    }

    private _clickBn:boolean;
    public get clickBn() : boolean {
        return this._clickBn
    }
    public set clickBn(v : boolean) {
        this._clickBn = v;
        this.btn.children[0].getComponent(Sprite).enabled = !v;
        this.btn.children[0].children[0].active = v;
    }
    
    
    private close() {
        this.node.destroy();
        mjways.main.freeStartBack();
    }

    private onClose() {
        if (this.showBtnBn) {
            this.clickBn = true;
            this.scheduleOnce(() => {
                this.close();
            }, 0.5)
        }
    }
}