import { _decorator, Component, Enum, Node, sp, Sprite, SpriteFrame } from 'cc';
import { instantiate } from 'cc';
import { Prefab } from 'cc';
import { tween } from 'cc';
import { Vec3 } from 'cc';
import { Animation } from 'cc';
import { baseSymbol, ESHOWTYPE } from '../../module_basic/ui_slot/match3/baseSymbol';

import { AnimationState } from 'cc';
import { reelDate } from '../../module_basic/ui_slot/match3/baseReel';
import { mjways } from './cMjways';


const { ccclass, property } = _decorator;

@ccclass('mjwaysSymbol')
export class mjwaysSymbol extends baseSymbol {
    @property({ type: Animation, tooltip: "" })
    public boom: Animation = null!;
    @property({ type: Node, tooltip: "" })
    public select: Node = null!;
    @property({ type: Node, tooltip: "" })
    public goldAni: Node = null!;
    @property({ type: Node, tooltip: "" })
    public bgAni: Node = null!;
    @property({ type: Node, tooltip: "" })
    public bg: Node = null!;


    protected start(): void {

    }

    bind(id: number, r: reelDate, parent: Node, pushBn: boolean, setPosBn: boolean = true) {
        super.bind(id, r, parent, pushBn, setPosBn);
        if (mjways.inFortune && r.index == 2) {
            this.goldBn = true;
        }
    }

    init() {
        super.init();
        this.bgAni.active = false;
        this.goldAni.active = false;
        this.selectBn = false;
        this.boom.node.active = false;
        this.icon.node.children[0].active = false;
        this.blur.node.children[0].active = false;
        this.icon.node.children[1].active = false;
        this.blur.node.children[1].active = false;
        if (super.symbolId)
            this.bg.active = !(super.symbolId == ESYMBOLID.wild || super.symbolId == ESYMBOLID.scatter || !super.symbolId);
    }

    unuse(): void {
        super.unuse();
        this.init();
        this.goldBn = false;
    }
    public get symbolId(): number {
        return super.symbolId;
    }
    public set symbolId(v: number) {
        super.symbolId = v;
        if (v == ESYMBOLID.wild) {
            this.icon.spriteFrame = undefined;
            this.icon.node.children[0].active = true;
            this.blur.spriteFrame = undefined;
            this.blur.node.children[0].active = true;
        }
        if (v == ESYMBOLID.scatter) {
            this.icon.spriteFrame = undefined;
            this.icon.node.children[1].active = true;
            this.blur.spriteFrame = undefined;
            this.blur.node.children[1].active = true;
        }
    }
    public get blurBn(): boolean {
        return super.blurBn;
    }
    public set blurBn(v: boolean) {
        super.blurBn = v;
        this.bg.children[0].active = !v && !this.goldBn;
        this.bg.children[1].active = !v && this.goldBn;
        this.bg.children[2].active = v && !this.goldBn;
        this.bg.children[3].active = v && this.goldBn;
    }
    /***/
    private _goldBn: boolean = false;
    public get goldBn(): boolean {
        return this._goldBn
    }
    public set goldBn(v: boolean) {
        this._goldBn = v;
        this.blurBn = this.blurBn;
    }

    private _selectBn: boolean = false;
    public get selectBn(): boolean {
        return this._selectBn;
    }
    public set selectBn(v: boolean) {
        this._selectBn = v;
        this.select.active = v;
    }

    removeOver() {
        if (this.changeBn()) {
        } else {
            let ani: Animation = this.node.getComponent(Animation);
            ani.stop();
        }
    }

    /** */
    playBoom() {
        if (this.selectBn == false) return;
        if (this.changeBn()) {
            this.goldAni.active = true;
            this.bg.active = false;
            // this.goldBn = false;
            // this.symbolId = ESYMBOLID.wild;
            // this.spawn();
        } else {
            mjways.audio.playSymbolShrink();
            this.bgAni.active = true;
            this.bg.active = false;
            this.boom.node.active = true;
            this.boom.on("finished" as any, () => {
                this.icon.node.scale_x = 0.7;
                this.recover();
                mjways.removeCount++;
                if (mjways.removeCount >= mjways.round.addItems.length)
                    mjways.reel.doAdd();
            }, this);
            this.boom.play();
        }
        this.selectBn = false;
        let ani: Animation = this.node.getComponent(Animation);
        ani.on("finished" as any, () => {
            this.goldBn = false;
            this.symbolId = ESYMBOLID.wild;
            this.spawn();
            mjways.removeCount++;
            if (mjways.removeCount >= mjways.round.addItems.length)
                mjways.reel.doAdd();
        }, this);
        ani.play();
    }

    remove() {
        this.node.parent = null;
    }

    /**wild */
    private changeBn(): boolean {
        return this.goldBn && this.symbolId != ESYMBOLID.wild;
    }

    /** wild*/
    spawn() {
        if (this.symbolId == ESYMBOLID.wild) {
            this.wildAni.play();
            mjways.audio.playSpawnWild();
        }
        if (this.symbolId == ESYMBOLID.scatter) {
            this.scatterAni.on("finished" as any, () => {
                this.playScatterIdle();
            })
            this.scatterAni.play("scatterspawn");
            mjways.audio.playSpawnScatter();
        }
    }

    private playScatterIdle() {
        this.scatterAni.play("scatteridle");
    }

    /** */
    playWin() {
        this.selectBn = true;
    }

    private get wildAni(): Animation {
        return this.icon.node.children[0].getComponent(Animation);
    }

    private get scatterAni(): Animation {
        return this.icon.node.children[1].getComponent(Animation);
    }

    onClick(e: TouchEvent) {
        mjways.main.addDetail(this);
    }
}

export enum ESYMBOLID {
    null,
    twoL,
    twoO,
    fiveL,
    fiveO,
    cracks,
    square,
    red,
    green,
    wild,
    scatter
}


