import { _decorator, Component, Node } from 'cc';
import { tween } from 'cc';
import { mjways } from './cMjways';
import { labNumShow } from '../../module_basic/utils/labNumShow';
import { Num } from '../../core_tgx/framework/util/Num';
const { ccclass, property } = _decorator;

@ccclass('mjwaysInfoBoard')
export class mjwaysInfoBoard extends Component {
    @property({ type: [Node], tooltip: "" })
    public bgs: Node[] = [];
    @property({ type: Node, tooltip: "" })
    public infos: Node = null!;
    @property({ type: Node, tooltip: "" })
    public infoWin: Node = null!;

    onLoad() {
        mjways.infoBoard = this;
    }

    protected start(): void {
        this.lev = 0;
        this.showInfosBn = true;
    }

    private _lev: number = 0;
    public get lev(): number {
        return this._lev
    }
    public set lev(v: number) {
        this._lev = v;
        this.bgs[0].active = v == 0;
        this.bgs[1].active = v == 1;
        this.bgs[2].active = v == 2;
    }

    /**winNode label */
    private playBn: boolean = false;
    playAnimWin(coin: number, playBn: boolean = true, showTwBn: boolean = false) {
        this.playBn = playBn;
        let bindWin: Function = () => {
            if (!showTwBn) {
                mjways.tempWin += coin;
                mjways.slotCtrl.lblUserCoin.string = Num.exchangeToThousands(mjways.exchangeRate,
                    mjways.lotteryRes.curCoin - mjways.lotteryRes.winCoin + mjways.tempWin);
                mjways.slotCtrl.winCoin_lab.string = Num.exchangeToThousands(mjways.exchangeRate, mjways.tempWin);
            }
        }
        this.showInfosBn = false;
        this.showTwBn = showTwBn;
        if (coin > mjways.betSum * 3 && coin < mjways.betSum * 5) {
            this.lev = 1;
        } else if (coin >= mjways.betSum * 5) {
            this.lev = 2;
        } else {
            this.lev = 0;
        }
        if (playBn) {
            tween(this.winLab)
                .to(0.5, { num: coin * 100 })
                .start();
        } else {
            this.winLab.setRealNum(coin);
        }
    }


    /** */
    private _showInfosBn: boolean = false;
    public get showInfosBn(): boolean {
        return this._showInfosBn
    }
    public set showInfosBn(v: boolean) {
        this._showInfosBn = v;
        this.infos.active = v;
        this.infoWin.active = !v;
        this.lev = 0;
    }

    /** */
    private _showTwBn: boolean = false;
    public get showTwBn(): boolean {
        return this._showTwBn;
    }
    public set showTwBn(v: boolean) {
        this._showTwBn = v;
        this.infoWin.getChildByName("info_win").active = !v;
        this.infoWin.getChildByName("info_tw").active = v;
    }

    private get winLab(): labNumShow {
        return this.infoWin.getChildByName("Label").getComponent(labNumShow);
    }
}