import { _decorator, Component, macro } from 'cc';
import { Num } from '../../core_tgx/framework/util/Num';
import { LanguageSprite } from 'db://assets/module_basic/scripts/language/LanguageSprite';
import { Label } from 'cc';
import { mjways } from './cMjways';
import { instantiate } from 'cc';
import { Prefab } from 'cc';
import { Node } from 'cc';
import { Vec3 } from 'cc';
import { tween } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('mjwaysBigwin')
export class mjwaysBigwin extends Component {
    @property({ type: Label, tooltip: "coin label" })
    public coinLabel: Label = null!;
    @property({ type: LanguageSprite, tooltip: "" })
    public bigWinBw: LanguageSprite = null!;

    onLoad() {
    }

    protected start(): void {
        this.init();
    }
    private init() {
        this.coin = 0;
        this.coinLabel.string = "0";
    }

    //
    private aimCoin: number = 0;
    private addcoin: number = 0;
    private addOnce: number = 0;
    private _playOverBn: boolean = true;
    public get playOverBn(): boolean {
        return this._playOverBn;
    }
    public set playOverBn(v: boolean) {
        this._playOverBn = v;
    }

    private _coin: number = 0;
    public get coin(): number {
        return this._coin;
    }
    public set coin(v: number) {
        this._coin = v;
    }

    bigwincoin(coin: number) {
        this.node.parent = mjways.main.node;
        mjways.audio.playBigwin();
        this.node.active = true;
        this.coin = coin;
        this.playBigWinCoin();
    }

    private playBigWinCoin() {
        let coin = this.coin;
        this.node.active = true;
        this.bigBn = false;
        this.superBn = false;
        this.megaBn = false;
        this.aimCoin = coin;
        this.addcoin = 0;
        this.addOnce = this.aimCoin / 100;
        this.playOverBn = false;
        this.coinLabel.string = Num.exchangeToThousands(mjways.exchangeRate, this.addcoin);
        this.schedule(this.coinOneChange, 0.05, macro.REPEAT_FOREVER);
        this.scheduleOnce(this.closeBigWin, 16);
    }

    private bigBn: boolean = false;
    private superBn: boolean = false;
    private megaBn: boolean = false;
    private coinOneChange() {
        this.addcoin += this.addOnce;
        if (this.addcoin >= this.aimCoin) {
            this.playBigWin(this.aimCoin);
            this.playCoinOver();
        } else {
            this.playBigWin(this.addcoin);
            this.coinLabel.string = Num.exchangeToThousands(mjways.exchangeRate, this.addcoin);
        }
    }

    private playCoinOver() {
        this.unschedule(this.coinOneChange);
        mjways.audio.playBigwinEnd();
        this.playOverBn = true;
        this.coinLabel.string = Num.exchangeToThousands(mjways.exchangeRate, this.aimCoin);
        this.unschedule(this.closeBigWin);
        this.scheduleOnce(this.closeBigWin, 5);
    }

    private playBigWin(coin: number) {
        if (coin >= mjways.betSum * 35 && this.megaBn == false) {
            this.playAnim_MegaWin();
            this.megaBn = true;
        } else if (coin >= mjways.betSum * 15 && coin < mjways.betSum * 35 && this.superBn == false) {
            this.playAnim_SuperWin();
            this.superBn = true;
        } else if (coin < mjways.betSum * 15 && this.bigBn == false) {
            this.playAnim_BigWin();
            this.bigBn = true;
        }
    }

    onFastBigWin(e: TouchEvent) {
        if (this.playOverBn) {
            this.closeBigWin();
        } else {
            this.playCoinOver();
        }
    }

    //BigWin
    private playAnim_BigWin() {
        this.bigWinBw.dataID = "bw_text";
    }

    //SuperWin
    private playAnim_SuperWin(coin: number = 0) {
        this.bigWinBw.dataID = "mw_text";
    }

    //MegaWin
    private playAnim_MegaWin(coin: number = 0) {
        this.bigWinBw.dataID = "smw_text";
    }

    private createCoins() {
        this.schedule(this.coinFactory, 0.2, macro.REPEAT_FOREVER);
    }

    /** */
    private coinNum: number = 0;
    private pb:Prefab;
    private coinFactory() {
        let node: Node = instantiate(this.pb);
        node.parent = this.node;
        this.coinNum++;
        node.setSiblingIndex(Math.random() > 0.5 ? this.coinNum + 1 : 0 + Math.floor(2 * Math.random()));
        let startPos: Vec3 = new Vec3(0,0);
        let endPos: Vec3 = new Vec3();
        endPos.x = startPos.x
        endPos.y = -1000;
        node.position = startPos;
        tween(node)
            .to(5, { position: endPos })
            .call(() => {
                node.parent = null;
                this.coinNum--;
            })
            .start();
    }

    closeBigWin() {
        this.unschedule(this.closeBigWin);
        mjways.audio.stopBigwinEnd();
        mjways.main.closeBigWin();
        this.node.destroy();
        mjways.bigwin = undefined;
    }
}