import { _decorator, Component } from 'cc';
import { Node } from 'cc';
import { tween } from 'cc';
import { Label } from 'cc';
import { labNumShow } from '../../module_basic/utils/labNumShow';
import { NodePool } from 'cc';
import { instantiate } from 'cc';
import { Prefab } from 'cc';
import { macro } from 'cc';
import { Vec3 } from 'cc';
import { bezierMove } from '../../module_basic/utils/bezierMove';
import { mjways } from './cMjways';
const { ccclass, property } = _decorator;


@ccclass('mjwaysFreeEnd')
export class mjwaysFreeEnd extends Component {
    @property({ type: Prefab, tooltip: "" })
    public coinPb: Prefab = null!;
    @property({ type: Node, tooltip: "" })
    public coins: Node = null!;
    @property({ type: labNumShow, tooltip: "" })
    public lab: labNumShow = null!;
    @property({ type: Node, tooltip: "" })
    public btn: Node = null!;
    protected start(): void {
        this.node.active = false;
        this.node.opacity = 0;
    }

    setParent(coin: number) {
        this.node.parent = mjways.slotCtrl.node;
        this.show(coin);
    }

    private aim: number;
    private show(coin: number) {
        // mjways.audio.playTotalwin();
        this.node.active = true;
        this.createCoin();
        this.aim = coin;
        this.showBtnBn = false;
        this.btn.opacity = 0;
        tween(this.node)
            .to(0.2, { opacity: 255 })
            .call(this.doShow, this)
            .start();
    }

    private doShow() {
        this.lab.setRealNum(0);
        tween(this.lab)
            .to(5, { num: this.aim * 100 })
            .call(()=>{
                this.unschedule(this.doCreate);
                // mjways.audio.playTotalwinEnd();
                tween(this.btn)
                    .to(0.4,{opacity:255,scale:new Vec3(1.2,1.2)})
                    .to(0.2,{scale:new Vec3(1,1)})
                    .call(()=>{
                        this.showBtnBn = true;
                        this.scheduleOnce(this.hide,5);
                    })
                    .start();
            })
            .start();
    }

    private maxNum: number = 20;
    private disTime: number = 1;
    private createCoin() {
        this.schedule(this.doCreate, this.disTime, macro.REPEAT_FOREVER);
    }

    private doCreate() {
        let num: number = Math.ceil(Math.random() * this.maxNum);
        for (let i = 0; i < num; i++) {
            let coin: Node = this.factory();
            coin.position = new Vec3(0, 0);
            coin.parent = this.coins;
            this.scheduleOnce(() => {
                this.coinMove(coin);
            }, Math.random() * this.disTime);
        }
    }

    private coinMove(coin: Node) {
        let radomSize: number = 0.4 + Math.random() * 0.6;
        let radomAngle: number = Math.random() * 360;
        coin.angle = radomAngle;
        coin.scale = new Vec3(radomSize, radomSize);
        let b: bezierMove = coin.getComponent(bezierMove);
        if (!b) b = coin.addComponent(bezierMove);
        let topNum: number = 500 + 200 * Math.random();
        let p0: Vec3 = coin.position;
        let p3: Vec3 = new Vec3(-600 + 1200 * Math.random(), -1000);
        let p1: Vec3 = new Vec3(p3.x / 4, topNum);
        let p2: Vec3 = new Vec3(p3.x, topNum);
        b.pArr = [p0, p1, p2, p3];
        b.t = 0;
        tween(b)
            .to(5, { t: 1 }, { easing: 'quartInOut' })
            .call(() => {
                this.recover(b.node);
            })
            .start();
    }

    private _pool: NodePool;
    private get pool(): NodePool {
        if (!this._pool) this._pool = new NodePool();
        return this._pool;
    }

    /** */
    private factory(): Node {
        let coin: Node = this.pool.get();
        if (!coin) {
            coin = instantiate(this.coinPb);
        }
        return coin;
    }

    /** */
    private recover(coin: Node) {
        this.pool.put(coin);
    }

    /** */
    private clearCoins() {
        for (let i = this.coins.children.length - 1; i >= 0; i--) {
            let coin: Node = this.coins.children[i];
            this.recover(coin);
        }
    }

    private hide() {
        this.unschedule(this.hide);
        tween(this.node)
            .to(0.2, { opacity: 0 })
            .call(() => {
                this.clearCoins();
                mjways.main.freeEndBack();
                this.node.destroy();
            })
            .start();
    }

    private showBtnBn: boolean = false;
    private onHide() {
        if (this.showBtnBn) {
            this.hide();
        }
    }
}