/*
histroy
*/
import { _decorator, Component, Prefab, Node, ScrollView } from 'cc';
import { tween } from 'cc';
import { Label } from 'cc';
import { Vec3 } from 'cc';
import { instantiate } from 'cc';
import { RoomMgr } from '../../module_basic/scripts/RoomMgr';
import { UIHelp, UIhelpParam } from '../../module_basic/ui_help/UIHelp';
import { ApiReturn } from 'tsrpc-browser';
import { ResPlayMatch3 } from '../../module_basic/shared/protocols/public/slots/PtlPlayMatch3';
import { gameNet } from '../../module_basic/scripts/NetGameServer';
import { SubGameDef } from '../../module_basic/scripts/SubGameDef';
import { mjwaysAudio } from './mjwaysAudio';
import { mjways } from './cMjways';
import { LanguageData } from '../../module_basic/scripts/language/LanguageData';
import { baseSymbol } from '../../module_basic/ui_slot/match3/baseSymbol';
import { Button } from 'cc';
import { mjwaysSymbol } from './mjwaysSymbol';
import { dataChange, Round } from './dataChange';
import { mjwaysFreeStart } from './mjwaysFreeStart';
import { Animation } from 'cc';
import { mjwaysDetail } from './mjwaysDetail';
import { macro } from 'cc';
import { Num } from '../../core_tgx/framework/util/Num';
import { mjwaysFreeEnd } from './mjwaysFreeEnd';

const { ccclass, property } = _decorator;

@ccclass('mjwaysMain')
export class mjwaysMain extends Component {
    @property({ type: Prefab, tooltip: "" })
    public bigwin: Prefab = null!;
    @property({ type: Prefab, tooltip: "" })
    public freeStart: Prefab = null!;
    @property({ type: Prefab, tooltip: "" })
    public freeEnd: Prefab = null!;
    @property({ type: Prefab, tooltip: "Pb" })
    public detailPb: Prefab = null!;
    @property({ type: Node, tooltip: "" })
    public slotCtrlNode: Node = null!;
    @property({ type: Node, tooltip: "" })
    public dark: Node = null!;
    public delayClick: boolean = false;

    onLoad() {
        mjways.main = this;
    }

    start() {
        this.dark.active = false;
        mjways.rollEnable = true;
    }

    onCLick(event: TouchEvent, args: any) {
        switch (args) {
            case "roll":
                this.startRoll();
                break;
            case "add":
                this.add();
                break;
            case "dec":
                this.dec();
                break;
            case "help":
                tgx.UIMgr.inst.showUI(UIHelp, null, this, new UIhelpParam("rule"));
                break;
            case "pay":
                tgx.UIMgr.inst.showUI(UIHelp, null, this, new UIhelpParam("payout"));
                break;
            case "lastmoney":
                mjways.lastmoneyPanel.showPanel();
                break;
            case "exitGame":
                this.onBtnExitClicked();
                break;
        }
    }

    onBtnExitClicked() {
        tgx.UIAlert.show(LanguageData.getLangByID("exit_game"), true).onClick(async (ok: Boolean) => {
            if (ok) {
                mjways.main.close();
                RoomMgr.inst.exitRoom();
            }
        });
    }

    startRoll() {
        if (this.delayClick || mjways.rollEnable == false) {
            return;
        }
        if (!mjways.auto) {
            this.delayClick = true;
            this.scheduleOnce(() => {
                this.delayClick = false;
            }, 1);
            if (mjways.status == 0) {
                mjways.status = 2;
                this.sendRoll();
            } else if (mjways.status == 1) {
                mjways.slotCtrl.setSpinAnim(3);
                this.stopImmediately();
            }
        }

    }

    add() {
        if (mjways.auto) {
            return;
        }
        mjways.sumIdx += 1;
        mjways.sumIdx = mjways.slotCtrl.updateBet(mjways.sumIdx) == 0 ? mjways.sumIdx - 1 : mjways.sumIdx;
        tween(mjways.slotCtrl.lblCurBet.node)
            .to(0.1, { scale: new Vec3(1.2, 1.2, 1.2) })
            .to(0.1, { scale: new Vec3(1, 1, 1) })
            .start()
    }

    dec() {
        if (mjways.auto) {
            return;
        }
        mjways.sumIdx -= 1;
        mjways.sumIdx = mjways.slotCtrl.updateBet(mjways.sumIdx) == 0 ? mjways.sumIdx + 1 : mjways.sumIdx;
        tween(mjways.slotCtrl.lblCurBet.node)
            .to(0.1, { scale: new Vec3(1.2, 1.2, 1.2) })
            .to(0.1, { scale: new Vec3(1, 1, 1) })
            .start()
    }

    startAuto(n: any) {
        if (this.delayClick) {
            return;
        }
        if (mjways.status == 0) {
            mjways.auto = true;
            mjways.autoTimes = n;
            (((mjways.slotCtrl.Btn_stopAuto as Node).children[0] as Node).getComponent(Label) as Label).string = n + "";
            if (n > 999) {
                ((mjways.slotCtrl.Btn_stopAuto as Node).children[0] as Node).scale = new Vec3(0.76, 0.76, 0.76);
                // ((mjways.slotCtrl.Btn_stopAuto as Node).children[1] as Node).scale = new Vec3(0.76, 0.76, 0.76);
            } else if (n > 99) {
                ((mjways.slotCtrl.Btn_stopAuto as Node).children[0] as Node).scale = new Vec3(0.9, 0.9, 0.9);
                // ((mjways.slotCtrl.Btn_stopAuto as Node).children[1] as Node).scale = new Vec3(0.9, 0.9, 0.9);
            }
            else {
                ((mjways.slotCtrl.Btn_stopAuto as Node).children[0] as Node).scale = new Vec3(1, 1, 1);
                // ((mjways.slotCtrl.Btn_stopAuto as Node).children[1] as Node).scale = new Vec3(1, 1, 1);
            }
            mjways.slotCtrl.Btn_stopAuto.active = mjways.auto;
            mjways.slotCtrl.Btn_start.active = !mjways.slotCtrl.Btn_stopAuto.active;
            this.scheduleOnce(this.sendRoll, 1.5);
        }
    }

    stopAuto() {
        mjways.auto = false;
        mjways.autoTimes = 0;
    }

    /** */
    private rollCallBackBn: boolean = false;
    rollCallBack() {
        let st = 0;
        mjways.status = st;
        if (mjways.status == 0) {
            if (this.rollCallBackBn) {
                return;
            }
            this.rollCallBackBn = true;
            this.scheduleOnce(() => {
                this.rollCallBackBn = false;
            }, 1)
            mjways.reel.removeRadom();
            if (mjways.result.winCoin > 0) {
                mjways.slotCtrl.setSpinAnim(3);
            } else {
                mjways.slotCtrl.setSpinAnim(2);
            }
            this.scheduleOnce(() => {
                let arr: mjwaysSymbol[] = mjways.reel.getSymbols as mjwaysSymbol[];
                for (let i = 0; i < arr.length; i++) {
                    arr[i].spawn();
                }
                this.roundShow();
            }, 1)
        }
    }

    private roundShow() {
        let round: Round = mjways.round;
        if (round.removeItems.length > 0) {
            this.showRemove();
            if (round.removeType) {
                mjways.audio.playVoxPayout(round.removeType - 1);
            } else {
                mjways.audio.playVoxPayoutMultiSym();
            }
        } else {
            this.settlement();
        }
    }

    private showRemove() {
        let remove: number[] = mjways.round.removeItems;
        if (remove.length > 1) {
            let arr: mjwaysSymbol[] = mjways.reel.getSymbols as mjwaysSymbol[];
            for (let i = 0; i < remove.length; i++) {
                let index: number = remove[i];
                arr[index].playWin();
            }
            if (mjways.round.winCoin > 0) {
                mjways.infoBoard.playAnimWin(mjways.round.winCoin, false);
            }
            this.scheduleOnce(this.doRemove, 3);
        } else {
            this.settlement();
        }
    }

    private doRemove() {
        mjways.reel.drop();
    }

    dropBack() {
        this.doNext();
    }

    private settlement() {
        this.roundSM();
    }

    private roundSM() {
        if (mjways.round.winCoin > 0) {
            mjways.infoBoard.playAnimWin(mjways.round.winCoin, false);
            this.scheduleOnce(this.roundSMNext, 3);
        } else {
            this.roundSMNext();
        }
    }

    private roundSMNext() {
        if (mjways.roundIndex >= mjways.result.rounds.length - 1) {
            if (mjways.result.winCoin > 0) {
                if (mjways.result.winCoin < 5 * mjways.betSum) {
                    if (mjways.result.winCoin <= 3 * mjways.betSum) {
                        //  mjways.main.audio.playPrizeSmallwin();
                    } else {
                        //  mjways.main.audio.playPrizeMedwinCounting();
                    }
                    mjways.infoBoard.playAnimWin(mjways.result.winCoin, true, true);
                    this.scheduleOnce(this.doRoundSMNext, 2);
                } else {
                    mjways.bigwin.bigwincoin(mjways.result.winCoin);
                }
            } else {
                this.doRoundSMNext();
            }
        }
    }

    private doRoundSMNext() {
        if (mjways.resultIndex == 0 && mjways.roundIndex == mjways.result.rounds.length - 1 && mjways.fortuneBn) {
            let freeStart: mjwaysFreeStart = instantiate(this.freeStart).getComponent(mjwaysFreeStart);
            freeStart.setParent();
        } else {
            if (mjways.round.addItems.length == 0) {
                this.doNext();
            }
        }
    }

    freeStartBack() {
        mjways.bg.mul.playFortune();
    }

    playFortuneBack() {
        this.doNext();
    }

    freeEndBack() {
        this.playRollOver();
    }

    private doNext() {
        if (mjways.roundIndex < mjways.result.rounds.length - 1) {
            this.doNextRound();
        } else {
            this.doNextResult();
        }
    }

    //
    private doNextRound() {
        mjways.roundIndex++;
        this.roundShow();
    }

    //
    private doNextResult() {
        mjways.infoBoard.showInfosBn = true;
        mjways.roundIndex = 0;
        mjways.resultIndex++;
        if (mjways.playOverBn) {
            if (mjways.lotteryRes.results.length == 1) {
                this.playRollOver();
            } else {
                let free: mjwaysFreeEnd = instantiate(this.freeEnd).getComponent(mjwaysFreeEnd);
                free.setParent(mjways.lotteryRes.winCoin);
            }
            return;
        }
        mjways.slotCtrl.lastNum = mjways.result.leftFreeCount - 1;
        mjways.reel.loop();
        let loopEnd: Function = () => {
            let delay: number = 1;
            this.scheduleOnce(() => {
                mjways.reel.loopEnd(mjways.round.symbols, mjways.round.golds, () => {
                    this.rollCallBack();
                });
            }, delay)
        }
        loopEnd();
    }

    closeBigWin() {
        mjways.infoBoard.playAnimWin(mjways.result.winCoin, false, true);
        if (mjways.resultIndex == 0 && mjways.roundIndex == mjways.result.rounds.length - 1 && mjways.fortuneBn) {
            let freeStart: mjwaysFreeStart = instantiate(this.freeStart).getComponent(mjwaysFreeStart);
            freeStart.setParent();
        } else {
            this.doNext();
        }
    }

    /** */
    playRollOver() {
        this.rollComplete();
        this.doAuto();
    }

    doAuto() {
        if (mjways.auto && mjways.autoTimes > 0) {
            this.sendRoll();
        }
    }
    /** */
    private rollComplete() {
        mjways.status = 0;
        mjways.slotCtrl.setSpinAnim(0);
        this.addDetailListener();
    }

    roll(list: number[], golds: number[]) {
        if (!mjways.auto) {
            mjways.rollEnable = true;
        }
        mjways.status = 1;
        mjways.reel.loopEnd(list, golds, () => { this.rollCallBack() });
    }



    sendRoll() {
        this.initRoll();
        mjways.autoTimes = mjways.autoTimes > 0 ? mjways.autoTimes - 1 : 0;
        if (mjways.autoTimes == 0) {
            mjways.auto = false;
        }
        mjways.slotCtrl.setSpinAnim(1);
        let autoTimesLab: Label = mjways.slotCtrl.Btn_stopAuto.children[0].getComponent(Label) as Label;
        autoTimesLab.string = mjways.autoTimes + "";
        // this.audio.playclickstart();
        if (mjways.autoTimes > 999) {
            mjways.slotCtrl.Btn_stopAuto.children[0].scale = new Vec3(0.5, 0.5);
        } else if (mjways.autoTimes > 99) {
            mjways.slotCtrl.Btn_stopAuto.children[0].scale = new Vec3(0.64, 0.64);
        }
        else {
            mjways.slotCtrl.Btn_stopAuto.children[0].scale = new Vec3(1, 1);
        }
        mjways.reel.loop();
        this.sendLottery();
    }

    firstSymbolOver() {
        mjways.reel.spawn();
        this.addDetailListener();
    }

    private addDetailListener() {
        let symbols: baseSymbol[] = mjways.reel.getSymbols;
        for (let i = 0; i < symbols.length; i++) {
            let symbol: baseSymbol = symbols[i];
            symbol.node.getComponent(Button).interactable = true;
        }
    }

    private removeDetailListener() {
        let symbols: baseSymbol[] = mjways.reel.getSymbols;
        for (let i = 0; i < symbols.length; i++) {
            let symbol: baseSymbol = symbols[i];
            symbol.darkBn = false;
            symbol.node.getComponent(Button).interactable = false;
        }
        this.dark.active = false;
        let detail: mjwaysDetail = mjways.detail;
        if (detail) detail.node.active = false;
    }

    addDetail(s: baseSymbol) {
        if (!mjways.rollEnable) return;
        let detail: mjwaysDetail = mjways.detail;
        if (!detail) {
            detail = instantiate(this.detailPb).getComponent(mjwaysDetail);
            detail.node.parent = this.node;
            mjways.detail = detail;
        }
        detail.node.active = true;
        let symbols: baseSymbol[] = mjways.reel.getSymbols;
        for (let i = 0; i < symbols.length; i++) {
            let symbol: baseSymbol = symbols[i];
            if (symbol.poolId != s.poolId) {
                symbol.darkBn = true;
            }
        }
        this.dark.active = true;
        detail.node.position = s.node.position;
        detail.node.y += mjways.reel.node.parent.y;
        detail.init(s as mjwaysSymbol);
    }

    /** */
    initRoll() {
        this.removeDetailListener();
        mjways.infoBoard.showInfosBn = true;
        mjways.tempWin = 0;
        mjways.slotCtrl.lblUserCoin.string = Num.exchangeToThousands(mjways.exchangeRate, mjways.money);
        mjways.slotCtrl.winCoin_lab.string = Num.exchangeToThousands(mjways.exchangeRate, 0);
    }

    async sendLottery() {
        let retSpin: ApiReturn<ResPlayMatch3> = await gameNet.PlayMatch3(SubGameDef.SLOTS_MJWAYS,mjways.betSum + "");
        console.log(retSpin);
        this.lotteryBack(retSpin);
    }

    private lotteryBack(data: any) {
        if (data && data.isSucc) {
            this.exchange(data.res as ResPlayMatch3);
            console.log(mjways.lotteryRes);
            mjways.money = mjways.lotteryRes.curCoin;
            mjways.slotCtrl.lblUserCoin.string = Num.exchangeToThousands(mjways.exchangeRate,
                mjways.lotteryRes.curCoin - mjways.lotteryRes.winCoin);
            mjways.resultIndex = 0;
            mjways.roundIndex = 0;
            if (mjways.slotCtrl.isSpeed) {
                this.roll(mjways.round.symbols, mjways.round.golds)
            } else {
                this.scheduleOnce(() => {
                    this.roll(mjways.round.symbols, mjways.round.golds)
                }, 1)
            }
        }
    }

    exchange(data: ResPlayMatch3) {
        let res = dataChange.change(data);
        mjways.lotteryRes = res;
    }

    playVoxRandom() {
        this.schedule(this.doPlayVoxRandom, 10, macro.REPEAT_FOREVER)
    }

    private doPlayVoxRandom() {
        // mjways.audio.playVoxRandom();
    }

    stopVoxRandom() {
        this.unschedule(this.doPlayVoxRandom);
    }

    stopImmediately() {

    }

    close() {

    }
}