import { Sprite } from 'cc';
import { _decorator, Component, Node } from 'cc';
import { mjwaysMul } from './mjwaysMul';
import { mjways } from './cMjways';

const { ccclass, property } = _decorator;

@ccclass('mjwaysBg')
export class mjwaysBg extends Component {
    @property({ type: mjwaysMul, tooltip: "" })
    public mul: mjwaysMul = null!;
    @property({ type: [Node], tooltip: "" })
    public nodes: Node[] = [];
    onLoad() {

    }

    protected start(): void {
        this.init();
    }

    init() {
        this.fortuneBn = false;
        mjways.bg = this;
    }

    /** */
    private _fortuneBn: boolean = false;
    public get fortuneBn(): boolean {
        return this._fortuneBn;
    }
    public set fortuneBn(v: boolean) {
        this._fortuneBn = v;
        this.mul.fortuneBn = v;
        this.doNodes((node: Node) => {
            node.children[0].active = v;
        })
        if (v == false) mjways.slotCtrl.lastNum = -1;
        mjways.audio.playBgm(v?1:0);
    }

    private doNodes(func: Function) {
        for (let i = 0; i < this.nodes.length; i++) {
            func(this.nodes[i], i);
        }
    }
}